import { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import useUserStore from '../store/users';
import { register as registerFirebase, login as loginFirebase, logout as logoutFirebase } from '@/services/firebase';

export default function useAuth() {
  const router = useRouter();
  const { user, setUser } = useUserStore(); 
  useEffect(() => {
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      try {
        const user = JSON.parse(storedUser);
        setUser(user);  
      } catch (error) {
        console.error("localStorage'dan kullanÄ±cÄ± alÄ±nÄ±rken hata:", error);
      }
    }
  }, [setUser]);

  async function register(formData: { 
    email: string; 
    password: string;  
    nickname: string; 
    phoneNumber: string; 
    countryCode: string; 
    uidCode: string; 
  }) {
    try {
      const user = await registerFirebase(formData.email, formData.password, {
        nickname: formData.nickname,
        phoneNumber: formData.phoneNumber,
        countryCode: formData.countryCode,
        uidCode: formData.uidCode,
      });

      setUser(user);
      localStorage.setItem("user", JSON.stringify(user)); 

      router.push("/login");  
    } catch (error) {
      console.error("KayÄ±t HatasÄ±:", error);
    }
  }

  async function login(email: string, password: string) {
    try {
      const user = await loginFirebase(email, password);
      setUser(user);
      localStorage.setItem("user", JSON.stringify(user));  
      return user; 
    } catch (error) {
      console.error("GiriÅŸ HatasÄ±:", error);
      throw error; 
    }
  }
  
  async function logout() {
    if (confirm("Ã‡Ä±kmak istediÄŸinizden emin misiniz?")) {
      try {
        await logoutFirebase();
        setUser(null); 
        localStorage.removeItem("user"); 
        router.push("/login");
      } catch (error) {
        console.error("Ã‡Ä±kÄ±ÅŸ HatasÄ±:", error);
      }
    }
  }

  return {
    user,
    register,
    login,
    logout,
  };
}
